/* background-color: rgba(255, 255, 255, 50); */
.about-me {
    display: flex;
    
}

.about-text {
    box-sizing: border-box;
    border: solid 20px rgb(255, 255, 120);
    width: 60%;
    background-color: rgba(255, 255, 255, 90%);
    padding: 50px;
    overflow-wrap: break-word;
    font-size: 40px;
    font-family: Arial, Helvetica, sans-serif;
}

.about-text__small {
    font-size: 20px;
    font-style: italic;
    font-family: "century gothic";
}

.my-photo {
    width: 40%;
}

@media (max-width: 1024px) {
    .about-me {
        flex-direction: column-reverse;
    }

    .about-text {
        width: 100%;
        font-size: 20px;
    }

    .my-photo {
        width: 60%;
        margin: 20px auto;
    }
}