.app-header {   
    padding-top: 10px;
    padding-left: 20px;
    height: 200px;
    background-color: #000;
    color: #fff;
    font-size: 25px;
    font-weight: 200;
    font-family: Verdana;
}

.app-header__text-1 {
    cursor: pointer;     
    display: inline;
    overflow-wrap: break-word;
}

.app-header__text-2 {
    position: relative;
    top: 50px;
    left: 200px;
    cursor: pointer;     
    display: inline-block;
    overflow-wrap: break-word;
}
.app-header__text-3 {
    position: relative;
    top: 100px;
    left: 400px;
    cursor: pointer;     
    display: inline-block;
    overflow-wrap: break-word;
}

.app-header__text-4 {
    padding-top: 100px;
    padding-left: 100px;
    color: #000;
    font-size: 30px;
    font-weight: 200;   
    display: inline-block;
}
.app-header__text-4 span {
    cursor: pointer;  
    padding: 10px;
    border: solid #f17cb1 5px;
    border-radius: 25px;
}

.crash {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
}

.crash__img{
    position: absolute;
    bottom: 0;
    right: 0;
}


@media (max-width: 1024px) {
   .app-header{
       font-size: 20px;
   }
   .app-header__text-2, .app-header__text-3 {
       position: static;
       width: 70%;
   }
   .app-header__text-4 {
    font-size: 20px;
    padding-top: 50px;
    padding-left: 50px}

    .crash__img{
        height: 50%;
    }
}

