.menuBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    font-size: 30px;
    background-color: black;
    color: white;
}

.menuBar ul {
    display: flex;
    justify-content: space-around;
    color: white;
}

.menuBar li  {
    cursor: pointer;
    list-style: none; 
}

.menuBar li:hover {
    color: rgb(255, 255, 120);;
}

.active {
    color: #f17cb1;
}



@media (max-width: 1024px) {
    .menuBar ul {
        flex-direction: column;
        padding: 0;
    }
    .menuBar li {
        margin: 20px auto;
        font-size: 20px;
    }
}