.main-info{
    box-sizing: border-box;
    margin: 0 auto;
    padding: 30px;
    width: 70%;
    height: 500px;
    font-size: 30px;
    background-color: rgba(255, 255, 255, 90%);
    font-family: "century gothic";
}
.main-info p {
    font-size: 15px;
}
.main-info span {
    font-size: 20px;
    font-weight: bold;
}

@media (max-width: 1024px) {
    .main-info {
        font-size: 20px;
        height: 100%;
    }
 
}